
.buyPage-content {
  height: calc(100vh - 0.92rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background-image: url('../../../static/img/college/buypage/Inviting_bg.png');
  background-size: 100%;
}
.buyPage-content .yiheng {
  overflow: hidden;
  white-space: nowrap;
}
.buyPage-content-pingtitle {
  width: 100%;
  height: 0.6rem;
  text-align: center;
  margin-top: 0.3rem;
  position: relative;
}
.buyPage-content-pingtitle-title {
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  font-size: 0.34rem;
  font-weight: bold;
}
.buyPage-content-pingtitle-line1,
.buyPage-content-pingtitle-line2 {
  width: 0.8rem;
  height: 0.02rem;
  background-color: #666666;
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
.buyPage-content-pingtitle-line1:after,
.buyPage-content-pingtitle-line2:after {
  content: "";
  background-color: #666;
  width: 0.08rem;
  height: 0.08rem;
  display: inline-block;
  position: absolute;
  border-radius: 100%;
  top: -0.03rem;
}
.buyPage-content-pingtitle-line1:after {
  right: -0.04rem;
}
.buyPage-content-pingtitle-line2:after {
  left: -0.04rem;
}
.buyPage-content-pingtitle-more {
  color: #333;
  font-size: 0.26rem;
  position: absolute;
  right: 0.3rem;
  top: 0.16rem;
}
.buyPage-content-list {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  height: 1rem;
  padding: 0.3rem 0;
  border-bottom: 1px solid #ECF8FF;
}
.buyPage-content-list-img {
  width: 1rem;
  height: 1rem;
  background-color: #eee;
  border-radius: 100%;
  display: inline-block;
  margin-right: 0.1rem;
}
.buyPage-content-list-img img {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
}
.buyPage-content-list-info {
  height: 1rem;
  display: inline-block;
}
.buyPage-content-list-info .buytablecell {
  display: table;
  height: 1rem;
}
.buyPage-content-list-info .buytablecell .buytablecell2 {
  display: table-cell;
  vertical-align: middle;
}
.buyPage-content-list-info-name {
  font-size: 0.3rem;
  color: #222;
  margin-bottom: 0.02rem;
}
.buyPage-content-list-info-num {
  font-size: 0.26rem;
  color: #888;
}
.buyPage-content-list-details {
  width: 1.2rem;
  height: 0.52rem;
  background-color: #EF4949;
  border-radius: 0.26rem;
  text-align: center;
  line-height: 0.52rem;
  color: #fff;
  font-size: 0.28rem;
  display: inline-block;
  vertical-align: top;
  float: right;
  margin-top: 0.24rem;
}
.buyPage-content-list:last-child {
  border-bottom: none;
}
.buyPage-content-shuoming {
  width: 6.18rem;
  height: 4.42rem;
  margin: 0 auto;
  background-repeat: no-repeat;
  background-image: url('../../../static/img/college/buypage/Inviting_camera.png');
  background-size: 100%;
}
.buyPage-content-shuoming-title {
  width: 100%;
  text-align: center;
  font-size: 0.32rem;
  color: #000;
  height: 1rem;
  line-height: 1.2rem;
}
.buyPage-content-shuoming-content {
  font-size: 0.26rem;
  color: #333;
  width: 5rem;
  margin: 0 auto;
  height: 2.8rem;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.buyPage-content-img1 {
  width: 4.3rem;
  height: 3.6rem;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
}
.buyPage-content-img1 img {
  width: 4.3rem;
}
.buyPage-content-invite {
  display: inline-block;
  vertical-align: top;
  width: calc(100% - 4.3rem);
  height: 3.6rem;
}
.buyPage-content-invite-table {
  height: 3.6rem;
  display: table;
}
.buyPage-content-invite-table-cell {
  display: table-cell;
  vertical-align: middle;
}
.buyPage-content-invite-table-cell-title {
  font-size: 0.74rem;
  font-weight: bold;
  color: #2D3C50;
  margin-bottom: 0.08rem;
}
.buyPage-content-invite-table-cell-jion {
  font-size: 0.4rem;
  color: #2D3C50;
}
.buyPage-content-invite-table-cell-jion span {
  background-image: url('../../../static/img/college/buypage/Inviting_kuang.png');
  height: 0.55rem;
  width: 1.9rem;
  background-size: 100%;
  display: inline-block;
  text-align: center;
  margin-left: 0.1rem;
  margin-bottom: 0.18rem;
}
.buyPage-content-invite-table-cell-crouse {
  width: 2.8rem;
  height: 0.72rem;
  background-color: #EF4949;
  border-radius: 0.36rem;
  font-size: 0.28rem;
  text-align: center;
  line-height: 0.72rem;
  overflow: hidden;
  color: #fff;
  -webkit-box-shadow: 0px 1px 3px #EF4949;
          box-shadow: 0px 1px 3px #EF4949;
}
.buyPage .collegeFooter {
  width: 100%;
  height: 0.92rem;
  position: absolute;
  bottom: 0;
}
.buyPage .collegeFooter .left {
  width: 55%;
  height: 100%;
  background-color: white;
  float: left;
  text-align: center;
  color: #333333;
  font-size: 0.26rem;
  line-height: 0.92rem;
}
.buyPage .collegeFooter .right {
  width: 45%;
  height: 100%;
  float: right;
  background-color: #EF4949;
  text-align: center;
  color: white;
  font-size: 0.26rem;
  line-height: 0.92rem;
}



