
.listOne{
		margin-left: 0.2rem;
}
.listTwo{
		float: right;
		margin-right: 0.3rem;
}
.listcss{
		height: 0.8rem;
	    width: 100%;
	    background: #fff;
	    line-height: 0.8rem;
	    font-size: 0.27rem;
	    color: #333;
	    border-top: 1px solid #e1e2e6;
}
.box{
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		    -ms-flex-direction: column;
		        flex-direction: column;
		font-size: 0.26rem;
		height: 100%;
		background: #f1f2f6;
		color: #333
}
.box_one,.box_two,.box_three,.box_four,.box_five{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: horizontal;
		-webkit-box-direction: normal;
		    -ms-flex-direction: row;
		        flex-direction: row;
		-webkit-box-pack: justify;
		    -ms-flex-pack: justify;
		        justify-content: space-between;
		font-size: 0.28rem;
		border-bottom: 1px solid #e1e2e6;
		height: 0.8rem;
		line-height: 0.8rem;
		padding:0 0.2rem;
		background: #fff;
}

	/*.iconfont{
		color: #CCCCCC;
	}*/
.bankcard input{
		height: 0.6rem;
		border: none;
		outline-style: none;
}
.login{
   	display: -webkit-box;
   	display: -ms-flexbox;
   	display: flex;
   	-webkit-box-orient: horizontal;
   	-webkit-box-direction: normal;
   	    -ms-flex-direction: row;
   	        flex-direction: row;
   	-webkit-box-pack: center;
   	    -ms-flex-pack: center;
   	        justify-content: center;
    margin-top: 1rem;
}
.bankcard button{
		width: 4rem;
		height: 0.8rem;
		font-size: 0.26rem;
		outline-style: none;
		border: none;
		border-radius: 0.4rem;
		background-color: #3385ff;
		color: white;
}
.bankTitle{
		height: 0.8rem;
	    width: 100%;
	    text-align: center;
	    background: #fff;
	    line-height: 0.8rem;
	    font-size: 0.3rem;
	    border-bottom: 1px solid #e1e2e6;
}
.bankchoose{
		height: 0.7rem;
	    width: 100%;
	    background: #fff;
	    border-top: 1px solid #e1e2e6;
	    line-height: 0.7rem;
	    padding-left: 0.2rem;
	    font-size: 0.28rem;
}
.bankchoose:nth-child(1){
		border-top: 0
}
.login button{
		width:2rem;
}
.bankcard .login button:nth-child(1){
		margin-right: 0.5rem;
}
.login button:nth-child(2){
		margin-left: 0.5rem;
		background: #fff;
		color: #333;
}
.yinsi{
		height: 0.8rem;
		width:100%;
		background: #fff;
		line-height: 0.8rem;
}
.yinsi span{
		width:46%;
		display: inline-block;
		margin-left: 0.2rem;
}
.yinsi span button{
		position: absolute;
}
.yinsi input{
		position: relative;
		top: 0.2rem;
		left: 0.2rem;
}
.yinsi p{
		display: inline-block;
		margin-left: 0.5rem;
}



