
.lookrecord {
  height: 100vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.lookrecord-list {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  border-bottom: 1px solid #eee;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
}
.lookrecord-list-cover {
  width: 2.52rem;
  height: 1.52rem;
  background-color: #eee;
  display: inline-block;
  overflow: hidden;
}
.lookrecord-list-cover img {
  width: 2.52rem;
}
.lookrecord-list-info {
  display: inline-block;
  float: right;
  vertical-align: top;
  width: 4.08rem;
  height: 1.52rem;
}
.lookrecord-list-info-title {
  font-size: 0.3rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  height: 0.8rem;
}
.lookrecord-list-info-time {
  font-size: 0.26rem;
  margin-top: 0.4rem;
  color: #666;
}
.lookrecord-list-info-time-time {
  display: inline-block;
}
.lookrecord-list-info-time-watch {
  display: inline-block;
  float: right;
}
.lookrecord-list-info-time-watch img {
  height: 0.22rem;
  margin-top: 0.15rem;
  margin-right: 0.06rem;
}
.lookrecord-list-info-time-watch i {
  font-size: 0.35rem;
}
.lookrecord-list-info-time-watch i span {
  font-size: 0.26rem;
  display: inline-block;
  margin-left: 0.1rem;
  position: relative;
  top: -0.03rem;
}



