
.signSuccess {
  height: 100vh;
  font-size: 0.28rem;
  position: relative;
}
.signSuccess-no {
  opacity: 0;
}
.signSuccess-content {
  width: 100%;
  height: calc(100vh - 0.9rem);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.signSuccess-content-img {
  width: 6.9rem;
  margin: 0.3rem 0.3rem 0;
}
.signSuccess-content-tips {
  width: 100%;
  text-align: center;
}
.signSuccess-content-tips img {
  width: 0.26rem;
  display: inline-block;
}
.signSuccess-content-tips .tipsCont {
  font-size: 0.26rem;
  display: inline-block;
  color: #666;
}
.signSuccess-content-legend {
  padding: 0.4rem 0.3rem;
  height: 3.5rem;
}
.signSuccess-content-legend h1 {
  font-size: 0.28rem;
  color: #333;
  margin-bottom: 0.28rem;
}
.signSuccess-content-legend p {
  font-size: 0.26rem;
  color: #666;
  line-height: 0.45rem;
}
.signSuccess .myLayer {
  position: relative;
  z-index: 998;
}
.signSuccess .myLayer .shade {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.7;
  /*z-index: 8;*/
  pointer-events: auto;
}
.signSuccess .myLayer .cancelBox {
  display: table;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  /*z-index: 9;*/
  pointer-events: none;
}
.signSuccess .myLayer .cancelBox-mask {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  /*z-index: 9;*/
}
.signSuccess .myLayer .cancelBox-content {
  display: inline-block;
  width: 4.2rem;
  height: auto;
  background-color: #fff;
  /*z-index: 999;*/
  border-radius: 0.06rem;
  padding: 0 0.4rem;
  pointer-events: auto;
}
.signSuccess .myLayer .cancelBox-content .cancelTitle {
  font-size: 0.36rem;
  color: #333;
  font-weight: 600;
  margin-top: 0.4rem;
}
.signSuccess .myLayer .cancelBox-content p {
  text-align: left;
  font-size: 0.3rem;
}
.signSuccess .myLayer .cancelBox-content .cancelCont {
  color: #333;
  margin: 0.3rem 0 0.2rem 0;
}
.signSuccess .myLayer .cancelBox-content .notice {
  font-size: 0.26rem;
  color: #999;
}
.signSuccess .myLayer .cancelBox-content .btn {
  width: 100%;
  height: 0.6rem;
  margin-top: 0.2rem;
}
.signSuccess .myLayer .cancelBox-content .btn span {
  float: left;
  width: 49.5%;
}
.signSuccess .myLayer .cancelBox-content .btn .cancelbtn {
  color: #999;
  border-right: 0.02rem solid #DDD;
}
.signSuccess .footerBtn {
  width: 100%;
  height: 0.9rem;
  position: absolute;
  bottom: 0;
  left: 0;
}
.signSuccess .footerBtn .cancel {
  width: 3rem;
  height: 0.9rem;
  color: #fff;
  font-size: 0.3rem;
  line-height: 0.9rem;
  text-align: center;
  background-color: #666;
}
.signSuccess .footerBtn .back {
  width: 4.5rem;
  height: 0.9rem;
  line-height: 0.9rem;
  font-size: 0.3rem;
  color: #fff;
  background-color: #222;
  text-align: center;
}
.signSuccess .footerBtn .back2 {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  font-size: 0.3rem;
  color: #fff;
  background-color: #222;
  text-align: center;
}
.signSuccess .left {
  float: left;
}
.signSuccess .right {
  float: right;
}
.signSuccess .disc {
  width: 3%;
}
.signSuccess .txt {
  width: 97%;
}



