
.couponsList {
  width: 100%;
  height: 7.1rem;
  background-color: #F5F5F5;
  position: fixed;
  bottom: 0;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
}
.couponsList-title {
  color: #333333;
  font-size: 0.38rem;
  height: 1.16rem;
  line-height: 1.16rem;
  padding-left: 0.32rem;
  font-weight: 900;
}
.couponsList-content {
  height: 4.72rem;
  overflow-y: scroll;
}
.couponsList-content-item {
  width: 6.9rem;
  height: 1.76rem;
  background: url('/static/img/home/coupon_bg1@2x.png');
  background-size: 100% 100%;
  margin: 0 auto 0.3rem;
  overflow: hidden;
}
.couponsList-content-item-left {
  width: 34%;
  height: 100%;
  text-align: center;
  float: left;
}
.couponsList-content-item-left p:nth-child(1) {
  color: #EF5D38;
  font-size: 0.38rem;
  margin-top: 0.5rem;
}
.couponsList-content-item-left p:nth-child(2) {
  color: #666666;
  font-size: 0.24rem;
}
.couponsList-content-item-right {
  width: 66%;
  height: 100%;
  text-align: center;
  float: right;
}
.couponsList-content-item-right p:nth-child(1) {
  font-size: 0.3rem;
  color: #222222;
  margin-top: 0.36rem;
}
.couponsList-content-item-right p:nth-child(2) {
  font-size: 0.24rem;
  color: #666666;
  margin-top: 0.1rem;
}
.couponsList-content-item-right p:nth-child(3) {
  font-size: 0.24rem;
  color: #666666;
}
.couponsList-btn {
  width: 6.9rem;
  height: 0.9rem;
  line-height: 0.9rem;
  text-align: center;
  color: white;
  background-color: #222222;
  font-size: 0.3rem;
  margin: 0.15rem auto;
}



