
.successEnter-content {
  height: calc(100vh - 0.9rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  text-align: center;
}
.successEnter-content-img {
  width: 6.9rem;
  margin: 0.3rem auto 0;
}
.successEnter-content-tip {
  margin-bottom: 0.3rem;
}
.successEnter-content-tip img {
  width: 0.22rem;
}
.successEnter-content-tip span {
  font-size: 0.26rem;
  color: #666;
}
.successEnter-btn {
  width: 100%;
  height: 0.9rem;
  background-color: #222222;
  text-align: center;
  color: #fff;
  font-size: 0.3rem;
  position: fixed;
  bottom: 0;
  line-height: 0.9rem;
}



