
.mySignup {
  padding: 0 0.3rem;
  font-size: 0.28rem;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.mySignup-no {
  opacity: 0;
}
.mySignup-tips {
  width: 100%;
  font-size: 0.26rem;
  color: #999999;
  padding: 0.3rem 0;
}
.mySignup-content {
  min-height: 100vh;
}
.mySignup-content-lists {
  height: 1.92rem;
  padding: 0.3rem 0.3rem 0 0.3rem;
  background: #fff;
  border-radius: 0.06rem;
  margin-bottom: 0.2rem;
}
.mySignup-content-lists-detail {
  float: left;
  width: 5.1rem;
  height: 100%;
}
.mySignup-content-lists-isSignup {
  float: left;
  display: inline-block;
  width: calc(100% - 5.1rem);
  height: calc(100% - 0.7rem);
  padding-top: 0.7rem;
}
.mySignup-activiesTitle {
  display: block;
  font-size: 0.32rem;
  color: #333333;
  margin-bottom: 0.32rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 98%;
}
.mySignup-startTime {
  display: block;
  font-size: 0.28rem;
  background: url('../../../static/img/college/mysignup/time_start.png') no-repeat left center;
  background-size: 0.24rem 0.25rem;
  text-indent: 0.38rem;
  margin-bottom: 0.22rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 98%;
}
.mySignup-address {
  display: block;
  font-size: 0.28rem;
  background: url('../../../static/img/college/mysignup/address_start.png') no-repeat left center;
  background-size: 0.22rem 0.27rem;
  text-indent: 0.38rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 98%;
}
.mySignup-btn {
  display: block;
  height: 0.42rem;
  line-height: 0.42rem;
  font-size: 0.26rem;
  text-align: center;
  background-color: #222222;
  color: #fff;
  border-radius: 0.06rem;
}
.mySignup .grey-font {
  color: #999;
}
.mySignup .grey-bg {
  background: #999;
}



