
.collegeHome {
  font-size: 0.24rem;
}
.collegeHome .backbtn {
  width: 100%;
  height: 0.92rem;
  position: absolute;
  bottom: 0rem;
  background-color: #222;
  z-index: 11;
  text-align: center;
  line-height: 0.92rem;
  color: #fff;
  font-size: 0.3rem;
}
.collegeHome .jieshao {
  width: 1.4rem;
  height: 0.6rem;
  position: absolute;
  z-index: 1;
  right: 0;
  color: #fff;
  line-height: 0.6rem;
  top: 1.45rem;
}
.collegeHome .jieshao img {
  width: 0.3rem;
  position: relative;
  top: 0.06rem;
}
.collegeHome .box1-bg {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 11;
  top: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.collegeHome .box1-tipbox {
  width: 6rem;
  height: 8rem;
  background-color: #fff;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 12;
  border-radius: 0.06rem;
}
.collegeHome .box1-tipclose {
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  color: #fff;
  border: 1px solid #fff;
  position: fixed;
  bottom: 0.6rem;
  z-index: 12;
  left: calc(50% - 0.3rem);
  text-align: center;
  line-height: 0.6rem;
  font-size: 0.3rem;
}
.collegeHome .askbtn {
  width: 1rem;
  height: 1rem;
  background-color: #fff;
  border-radius: 50%;
  position: fixed;
  z-index: 1;
  bottom: 1.13rem;
  text-align: center;
  line-height: 1rem;
  font-size: 0.30rem;
  font-weight: bold;
  -webkit-box-shadow: 0 0 0.1rem #ccc;
          box-shadow: 0 0 0.1rem #ccc;
  right: 0.3rem;
}
.collegeHome-share {
  width: 1.4rem;
  height: 0.46rem;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.46rem;
  color: #fff;
  overflow: hidden;
  background-color: #EF4949;
  border-top-left-radius: 0.23rem;
  border-bottom-left-radius: 0.23rem;
  position: absolute;
  top: 0.4rem;
  right: 0;
  z-index: 2;
}
.collegeHome-content {
  height: calc(100vh - 0.92rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  position: relative;
}
.collegeHome-content-content {
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  position: relative;
}
.collegeHome-content-content .btn {
  position: absolute;
  left: 50%;
  margin-left: -0.33rem;
  top: 4.6rem;
  z-index: 2;
  width: 0.66rem !important;
}
.collegeHome-content-content .videoFfenmian {
  width: 6rem;
  height: 3.5rem;
  position: absolute;
  left: 50%;
  margin-left: -3rem;
  background: url(/static/img/college0810/fengmiantu.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  top: 3.3rem;
  z-index: 1;
}
.collegeHome-content-content video,
.collegeHome-content-content iframe {
  width: 6rem;
  height: 3.5rem;
  position: absolute;
  left: 50%;
  margin-left: -3rem;
  top: 3.3rem;
  border: none;
}
.collegeHome-content-content img {
  width: 100% !important;
  height: auto !important;
}
.collegeHome-content-home {
  height: calc(100vh - 0.9rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.collegeHome-content-home-head {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  height: 2.5rem;
  width: 100%;
  background-image: url('../../../static/img/college/college_bg.png');
  background-size: 100%;
  margin-bottom: 0.2rem;
}
.collegeHome-content-home-head-img {
  width: 1.24rem;
  height: 1.24rem;
  border-radius: 100%;
  background-color: #eee;
  border: 1px solid #fff;
  margin: 0.63rem 0.3rem 0;
}
.collegeHome-content-home-head-img img {
  width: 1.24rem;
  height: 1.24rem;
  border-radius: 100%;
}
.collegeHome-content-home-head-info {
  color: #fff;
  margin-top: 0.63rem;
}
.collegeHome-content-home-head-info-name {
  font-size: 0.38rem;
  font-weight: bold;
}
.collegeHome-content-home-head-info-num {
  margin: 0.08rem 0;
}
.collegeHome-content-home-head-info-num,
.collegeHome-content-home-head-info-time {
  font-size: 0.26rem;
}
.collegeHome-content-home-course {
  background-color: #fff;
  overflow: auto;
  margin-bottom: 0.2rem;
}
.collegeHome-content-home-course-title {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.35rem 0;
}
.collegeHome-content-home-course-title-left {
  font-size: 0.44rem;
  color: #222;
  font-weight: bold;
}
.collegeHome-content-home-course-title-right {
  font-size: 0.26rem;
  margin-top: 0.15rem;
}
.collegeHome-content-home-course-title-right img {
  width: 0.26rem;
}
.collegeHome-content-home-course-title-right span {
  display: inline-block;
  vertical-align: top;
}
.collegeHome-content-home-course-pic {
  width: 6.9rem;
  height: 4rem;
  background-color: #eee;
  border-radius: 0.06rem;
  margin: 0 auto;
  margin-bottom: 0.25rem;
  overflow: hidden;
}
.collegeHome-content-home-course-pic img {
  width: 6.9rem;
}
.collegeHome-content-home-course-biaoti {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  font-size: 0.3rem;
  width: 6.9rem;
  margin: 0 auto;
}
.collegeHome-content-home-course-biaoti > div:nth-child(1) {
  width: 5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.collegeHome-content-home-course-biaoti > div:nth-child(1) div {
  width: 5rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.collegeHome-content-home-course-biaoti > div:nth-child(2) {
  background-color: #222;
  color: #fff;
  width: 1.62rem;
  height: 0.72rem;
  border-radius: 0.06rem;
  text-align: center;
  overflow: hidden;
  line-height: 0.72rem;
}
.collegeHome-content-home-course-desc {
  font-size: 0.26rem;
  width: 6.9rem;
  margin: 0 auto;
  color: #999;
  margin-top: 0.08rem;
  padding-bottom: 0.3rem;
}
.collegeHome-content .collegeFooter {
  width: 100%;
  height: 0.92rem;
  position: absolute;
  bottom: 0.06rem;
}
.collegeHome-content .collegeFooter .left {
  width: 55%;
  height: 100%;
  background-color: white;
  float: left;
  text-align: center;
  color: #333333;
  font-size: 0.26rem;
  line-height: 0.92rem;
}
.collegeHome-content .collegeFooter .right {
  width: 45%;
  height: 100%;
  float: right;
  background-color: #EF4949;
  text-align: center;
  color: white;
  font-size: 0.26rem;
  line-height: 0.92rem;
}
.collegeHome .fade-enter-active,
.collegeHome .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}
.collegeHome .fade-enter,
.collegeHome .fade-leave-to {
  opacity: 0;
}
.collegeHome .bounce-enter-active {
  -webkit-animation: bounce-in 0.5s;
          animation: bounce-in 0.5s;
}
.collegeHome .bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@-webkit-keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes bounce-in {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}



