
.money {
  height: 100vh;
  background-color: #fff;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.money-shuoming {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  padding-bottom: 1rem;
}
.money-shuoming p {
  font-size: 0.28rem;
  margin-bottom: 0.1rem;
}
.money-shuoming div {
  font-size: 0.26rem;
  color: #999;
}
.money-rechargeBtn {
  height: 0.92rem;
  width: 6.92rem;
  color: #fff;
  margin: 0 auto;
  background-color: #222;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.92rem;
  overflow: hidden;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
  border-radius: 0.06rem;
}
.money .balance {
  font-size: 0.76rem;
  text-align: center;
  font-weight: 500;
  margin-top: 0.9rem;
}
.money .Instructions {
  font-size: 0.26rem;
  color: #999;
  text-align: center;
  margin-bottom: 0.8rem;
}
.money .line {
  width: calc(100% - 0.3rem);
  margin: 0 auto;
  height: 1px;
  background-color: #eee;
}
.money-title {
  font-size: 0.28rem;
  color: #333;
  margin-left: 0.3rem;
  margin-top: 0.3rem;
}
.money-recahnge {
  width: 2.12rem;
  height: 1.32rem;
  border-radius: 0.06rem;
  display: inline-block;
  float: left;
  margin-left: 0.26rem;
  margin-top: 0.3rem;
  font-size: 0.28rem;
  color: #999;
  position: relative;
}
.money-recahnge-kb {
  font-weight: bold;
}
.money-recahnge-gift {
  position: absolute;
  left: 0;
  top: 0;
  width: 1rem;
}
.money-recahnge-gift img {
  width: 1rem;
}
.money-recahnge-gift span {
  position: absolute;
  top: 0;
  font-size: 0.22rem;
  color: #fff;
  margin-left: 0.13rem;
}
.money-recahnge-table {
  height: 1.32rem;
  display: table;
  width: 2.12rem;
  text-align: center;
}
.money-recahnge-choose {
  width: 0;
  height: 0;
  border-bottom: 0.5rem solid #000;
  border-left: 0.5rem solid transparent;
  position: absolute;
  bottom: 0;
  right: 0;
}
.money-recahnge-choose i {
  color: #fff;
  position: absolute;
  left: -0.3rem;
  top: 0.18rem;
  font-size: 0.3rem;
  font-weight: bold;
}
.money-recahnge-tablecell {
  display: table-cell;
  vertical-align: middle;
}



