
.buyRecord {
  width: 100%;
  height: 0.92rem;
  font-size: 0.28rem;
}
.buyRecord-title {
  width: 100%;
  height: 0.92rem;
  background-color: #fff;
  white-space: nowrap;
  overflow: hidden;
  margin-bottom: 0.2rem;
}
.buyRecord-title-list {
  display: inline-block;
  vertical-align: top;
  width: 33.33333333%;
  height: 0.92rem;
  text-align: center;
  line-height: 0.92rem;
  position: relative;
}
.buyRecord-title-list span {
  width: 0.52rem;
  height: 0.04rem;
  background-color: #222;
  position: absolute;
  bottom: 0;
  left: calc(50% - 0.26rem);
}
.buyRecord-box {
  overflow: auto;
  width: 100%;
  height: calc(100vh - 1.03rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.buyRecord-box-list {
  height: 1.56rem;
  width: 100%;
  background-color: #fff;
  color: #333;
}
.buyRecord-box-list-box {
  height: calc(1.56rem - 1px);
  width: calc(100% - 0.6rem);
  border-bottom: 1px solid #eee;
  margin: 0 auto;
}
.buyRecord-box-list-box-left {
  width: 5rem;
  display: inline-block;
}
.buyRecord-box-list-box-left .table1 {
  height: 1.56rem;
  display: table;
}
.buyRecord-box-list-box-left .tablecell1 {
  display: table-cell;
  vertical-align: middle;
}
.buyRecord-box-list-box-left-title {
  width: 5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 0.3rem;
  margin-bottom: 0.1rem;
}
.buyRecord-box-list-box-left-time {
  color: #666;
  font-size: 0.26rem;
}
.buyRecord-box-list-box-right {
  display: inline-block;
  vertical-align: top;
  line-height: 1.56rem;
  color: #999;
  float: right;
}



