
.payCoupon {
  font-size: 0.24rem;
  color: #333;
  width: 100%;
  background-color: #fff;
  position: fixed;
  z-index: 1;
  bottom: 0;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  padding-bottom: 0.3rem;
  background-color: #fcfcfc;
}
.payCoupon-title {
  font-size: 0.38rem;
  color: #333;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  padding: 0.4rem 0;
  border-bottom: 1px solid #eee;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: bold;
}
.payCoupon-price {
  font-size: 0.28rem;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  padding: 0.4rem 0;
  border-bottom: 1px solid #eee;
}
.payCoupon-price-title {
  color: #222;
  margin-right: 0.3rem;
}
.payCoupon-price-price1 {
  font-weight: bold;
  margin-right: 0.3rem;
}
.payCoupon-price-sale {
  color: #666;
  text-decoration: line-through;
}
.payCoupon-payType {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  font-size: 0.28rem;
  padding: 0.4rem 0;
  border-bottom: 1px solid #eee;
}
.payCoupon-payType-selectCoupon1 {
  float: right;
  color: #EF4949;
  font-size: 0.28rem;
}
.payCoupon-payType-selectCoupon1 img {
  width: 0.14rem;
  height: 0.24rem;
}
.payCoupon-payType-selectCoupon2 {
  float: right;
  color: #999999;
  font-size: 0.28rem;
}
.payCoupon-payType-selectCoupon2 img {
  width: 0.14rem;
  height: 0.24rem;
}
.payCoupon-payType-price {
  font-weight: bold;
  margin-left: 0.3rem;
}
.payCoupon-payType-role {
  font-size: 0.24rem;
  color: #EF4949;
  border: 1px solid #EF4949;
  padding: 0.04rem 0.1rem;
  margin-left: 0.3rem;
  border-radius: 0.06rem;
}
.payCoupon-payType-select {
  display: inline-block;
  vertical-align: top;
  float: right;
  width: 0.4rem;
  height: 0.4rem;
  border: 1px solid #222;
  border-radius: 50%;
  position: relative;
}
.payCoupon-payType-select input {
  position: absolute;
  top: -0.05rem;
  width: 0.5rem;
  height: 0.5rem;
  left: -0.05rem;
  opacity: 0;
}
.payCoupon-payType-select i {
  color: #fff;
  height: 0.3rem;
  width: 0.3rem;
  margin-left: 0.05rem;
  margin-top: 0.05rem;
  display: inline-block;
  font-size: 0.32rem;
}
@media screen and (min-width: 414px) {
.payCoupon-payType-select i {
    margin-top: 0.03rem;
}
}
.payCoupon-payType .selected {
  background-color: #222;
}
.payCoupon-payBtn {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  height: 0.92rem;
  background-color: #222;
  border-radius: 1px;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.92rem;
  overflow: hidden;
  margin-top: 0.05rem;
  border-radius: 0.06rem;
}



