
#customPage {
  overflow: hidden;
  /*padding-bottom: 1.5rem;*/
  font-size: 0.24rem;
  /**/
}
#customPage .customPage_null {
  width: 100%;
  height: 100vh;
  text-align: center;
  font-size: 0.38rem;
  color: #999999;
}
#customPage .customPage_null .icon-empty {
  font-size: 1.8rem;
  color: #999999;
}
#customPage .customPage_null p {
  padding-top: 50%;
}
#customPage .customPage_video-wrap {
  padding: 0.1rem;
  background-color: transparent;
}
#customPage .customPage_video-cover {
  position: absolute;
  width: 100%;
  height: 100%;
}
#customPage .customPage_video-coverimg {
  width: 100%;
  height: 100%;
}
#customPage .customPage_video-backColor {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
}
#customPage .customPage_video-play {
  position: absolute;
  left: 45%;
  top: 40%;
}
#customPage .customPage_video-play .icon-8shipinicon {
  font-size: 1rem;
  color: #0086b3;
}
#customPage .customPage_video-iframe {
  width: 100%;
  height: 4.5rem;
  border: 0;
}



