
.footer {
  color: #333;
  font-size: 0.26rem;
  text-align: center;
  padding-bottom: 0.4rem;
  padding-top: 0.4rem;
}
.footer .ge {
  display: inline-block;
  margin-right: 0.1rem;
  margin-left: 0.1rem;
}
.footer-bg {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 888;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
}
.footer-code {
  position: fixed;
  z-index: 999;
  width: 5rem;
  height: 5.5rem;
  background-color: #fff;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-image: url(/static/img/college/my_QRcode_bg.png);
  background-size: 100%;
  border-radius: 0.06rem;
}
.footer-code-title {
  font-size: 0.38rem;
  margin-top: 0.55rem;
}
.footer-code-code {
  width: 3rem;
  height: 3rem;
  margin: 0 auto;
  margin-top: 0.25rem;
}
.footer-code-tip {
  font-size: 0.3rem;
  margin-top: 0.25rem;
}
.footer-code-close {
  width: 0.3rem;
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
}



