
.coupin-null {
  height: 0.9rem;
}
.coupin-tab {
  width: 100%;
  overflow: hidden;
  background: #fff;
  position: fixed;
  top: 0;
  z-index: 99;
}
.coupin-tab .select {
  color: #222222;
}
.coupin-tab div {
  font-size: 0.3rem;
  color: #888888;
  float: left;
  width: 33.33%;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  position: relative;
}
.coupin-tab div .line {
  width: 0.6rem;
  height: 0.04rem;
  background-color: #222222;
  position: absolute;
  bottom: 0;
  left: calc(50% - 0.3rem);
  border-radius: 4rem;
}
.coupin-bottom {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  color: white;
  background: #222222;
  text-align: center;
  font-size: 0.3rem;
  position: fixed;
  bottom: 0;
}



