
.scholarship-sum {
  height: 3.82rem;
  width: 100%;
  background-color: #fff;
  position: relative;
}
.scholarship-sum-money {
  font-size: 0.64rem;
  text-align: center;
  padding-top: 0.9rem;
}
.scholarship-sum-shuoming {
  font-size: 0.26rem;
  text-align: center;
  color: #999;
}
.scholarship-sum-record {
  width: 1.42rem;
  height: 0.46rem;
  background-color: #666;
  border-top-left-radius: 0.23rem;
  border-bottom-left-radius: 0.23rem;
  font-size: 0.26rem;
  text-align: center;
  line-height: 0.46rem;
  overflow: hidden;
  position: absolute;
  right: 0;
  top: 0.4rem;
  color: #fff;
}
.scholarship-sum-btn {
  font-size: 0.26rem;
  width: 2.32rem;
  height: 0.72rem;
  background-color: #222;
  border-radius: 0.03rem;
  margin: 0 auto;
  margin-top: 0.6rem;
  color: #fff;
  text-align: center;
  line-height: 0.72rem;
  overflow: hidden;
  border-radius: 0.06rem;
}
.scholarship-sum span {
  position: absolute;
  right: 0.3rem;
  font-size: 0.26rem;
  color: #EF4949;
  top: 2.8rem;
}
.scholarship-list {
  background-color: #fff;
  margin-top: 0.2rem;
}
.scholarship-list .scholarshipheight {
  height: calc(100vh - 4.85rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.scholarship-list .icon-sanjiaoxing-right {
  font-size: 0.3rem;
  margin-right: 0.1rem;
  margin-left: 0.2rem;
  position: relative;
  top: -0.05rem;
}
.scholarship-list-title {
  font-size: 0.38rem;
  color: #222;
  padding-top: 0.32rem;
  font-weight: 500;
  padding-bottom: 0.3rem;
}
.scholarship-list-title span {
  display: inline-block;
  font-size: 0.26rem;
  color: #666;
  float: right;
  line-height: 0.7rem;
  margin-right: 0.3rem;
}
.scholarship-list-list {
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  border-bottom: 1px solid #eee;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
}
.scholarship-list-list-img {
  display: inline-block;
  vertical-align: top;
  width: 2.52rem;
  height: 1.52rem;
  overflow: hidden;
  margin-right: 0.05rem;
  border-radius: 0.06rem;
  background-color: #eee;
}
.scholarship-list-list-img img {
  width: 2.52rem;
}
.scholarship-list-list-info {
  display: inline-block;
  vertical-align: top;
}
.scholarship-list-list-info-title {
  width: 4.08rem;
  font-size: 0.3rem;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
  height: 0.8rem;
}
.scholarship-list-list-info-studyed {
  font-size: 0.26rem;
  color: #666;
  margin-top: 0.12rem;
}
.scholarship-list-list-info-pre {
  width: 4.12rem;
  height: 0.06rem;
  border-radius: 0.03rem;
  background-color: #ccc;
  margin-top: 0.14rem;
  overflow: hidden;
}
.scholarship-list-list-info-pre-preed {
  height: 100%;
  background-color: #000;
  border-radius: 0.03rem;
}
.scholarship-list-list-info-status {
  font-size: 0.26rem;
  color: #666;
  margin-top: 0.35rem;
}
.scholarship-list-list-info-status-btn {
  width: 1.32rem;
  height: 0.46rem;
  border: 1px solid #222;
  display: inline-block;
  text-align: center;
  line-height: 0.46rem;
  overflow: hidden;
  border-radius: 0.06rem;
  vertical-align: middle;
  color: #222;
  float: right;
  margin-top: -0.1rem;
}



