
.saleCard {
  font-size: 0.24rem;
  color: #333;
  text-align: center;
  background-color: #fff;
  height: 100vh;
}
.saleCard-img {
  /*width: 5.8rem;*/
  /*margin-top: 0.30rem;*/
  /*height: 9rem;*/
  width: 5rem;
}
.saleCard-tip1 {
  font-size: 0.3rem;
  color: #333;
  /*margin-top: 0.16rem;*/
  margin-top: -0.05rem;
}
.saleCard-tip2 {
  font-size: 0.26rem;
  color: #666;
  /*margin-top: 0.06rem;*/
}
.saleCard .bottom {
  width: calc(100% - 0.34rem);
  padding: 0 0.17rem;
  overflow-x: scroll;
  position: fixed;
  bottom: 0;
  background-color: #fff;
  /*height: 1.536rem;*/
  height: 1.2rem;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}
.saleCard .bottom div:first-child {
  margin-left: 0.2rem;
}
.saleCard .bottom div {
  display: inline-block;
  margin-right: 0.2rem;
  /*margin-top: 0.235rem;*/
  width: 1.066rem;
  height: 1.066rem;
  position: relative;
}
.saleCard .bottom div .select1 {
  width: 100%;
}
.saleCard .bottom div .select {
  position: absolute;
  width: 0.3rem;
  right: 0.1rem;
  bottom: 0.17rem;
  z-index: 1;
}



