
.personalcenter-bg {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.6);
  top: 0;
}
.personalcenter-code {
  position: fixed;
  z-index: 2;
  width: 5rem;
  height: 5.5rem;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border-radius: 0.06rem;
  background-image: url(/static/img/college/my_QRcode_bg.png);
  background-size: 100%;
  background-position-y: -0.1rem;
}
.personalcenter-code-title {
  font-size: 0.38rem;
  margin-top: 0.55rem;
  text-align: center;
}
.personalcenter-code-code {
  width: 3rem;
  height: 3rem;
  margin-top: 0.25rem;
  margin-left: 1rem;
}
.personalcenter-code-tip {
  font-size: 0.3rem;
  margin-top: 0.25rem;
  text-align: center;
}
.personalcenter-code-close {
  width: 0.3rem;
  position: absolute;
  top: 0.3rem;
  right: 0.3rem;
}
.personalcenter .mb20 {
  margin-bottom: 0.2rem;
}
.personalcenter-neirong {
  padding-bottom: 0.95rem;
}
.personalcenter-info {
  height: 3rem;
  width: 100%;
  background-position: center;
  background-size: 100%;
  margin-bottom: 0.2rem;
  background-image: url(data:image/png;base64,);
  position: relative;
}
.personalcenter-info-info {
  height: 1.26rem;
  position: absolute;
  bottom: 0.3rem;
  width: calc(100% - 0.6rem);
  left: 0.3rem;
}
.personalcenter-info-info img {
  width: 1.26rem;
  height: 1.26rem;
  border-radius: 50%;
  border: 1px solid #fff;
  margin-right: 0.16rem;
}
.personalcenter-info-info-name {
  width: 5rem;
  display: inline-block;
  font-size: 0.38rem;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.personalcenter-info-info-edit {
  width: 5rem;
  display: inline-block;
  font-size: 0.26rem;
  color: #fff;
  vertical-align: top;
}
.personalcenter-info-info i {
  color: #fff;
  font-size: 0.22rem;
  display: inline-block;
  vertical-align: top;
  line-height: 1.26rem;
  float: right;
}
.personalcenter-list {
  height: 0.9rem;
  width: 100%;
  background-color: #fff;
}
.personalcenter-list img {
  height: 0.38rem;
  margin-top: 0.26rem;
  margin-left: 0.3rem;
  float: left;
}
.personalcenter-list-right {
  width: 6.58rem;
  display: inline-block;
  font-size: 0.28rem;
  overflow: hidden;
  border-bottom: 1px solid #eee;
  vertical-align: top;
  float: right;
}
.personalcenter-list-right-name {
  line-height: 0.9rem;
  display: inline-block;
  vertical-align: top;
}
.personalcenter-list-right-right {
  display: inline-block;
  float: right;
  vertical-align: top;
  line-height: 0.9rem;
}
.personalcenter-list-right-right span {
  margin-right: 0.15rem;
}
.personalcenter-list-right-right i {
  font-size: 0.22rem;
  margin-right: 0.3rem;
  color: #999;
}



