
.confirmSignup {
  height: 100vh;
  font-size: 0.28rem;
  position: relative;
}
.confirmSignup-no {
  opacity: 0;
}
.confirmSignup-content {
  width: 100%;
  height: calc(100vh - 0.9rem);
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.confirmSignup-content-banner {
  width: 100%;
  height: 6.52rem;
  position: relative;
}
.confirmSignup-content-banner img {
  width: 100%;
}
.confirmSignup-content-banner .confirmInfo {
  position: absolute;
  left: 0.3rem;
  bottom: 0;
}
.confirmSignup-content-banner .confirmInfo .infoTitle {
  color: #fff;
  font-size: 0.42rem;
  margin-bottom: 0.4rem;
}
.confirmSignup-content-banner .confirmInfo .infoBox {
  color: #fff;
  margin-bottom: 0.3rem;
  width: 100%;
}
.confirmSignup-content-banner .confirmInfo .infoBox img {
  width: 0.28rem;
  margin-right: 0.2rem;
}
.confirmSignup-content-banner .confirmInfo .infoBox_address {
  max-width: 90%;
}
.confirmSignup-content-banner .confirmInfo .addressCont {
  min-height: 0.5rem;
}
.confirmSignup-content-legend {
  padding: 0.4rem 0.3rem;
}
.confirmSignup-content-legend h1 {
  font-size: 0.28rem;
  color: #333;
  margin-bottom: 0.28rem;
}
.confirmSignup-content-legend p {
  font-size: 0.26rem;
  color: #666;
  line-height: 0.45rem;
}
.confirmSignup .footerBtn {
  width: 100%;
  height: 0.9rem;
  position: absolute;
  bottom: 0;
  left: 0;
}
.confirmSignup .footerBtn-mainVenue {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  display: block;
  text-align: center;
  font-size: 0.28rem;
  color: #fff;
  background-color: #222;
}
.confirmSignup .footerBtn-mainVenue-none {
  background-color: #999;
}
.confirmSignup .fillInformation {
  position: relative;
  z-index: 998;
  font-size: 0.28rem;
}
.confirmSignup .fillInformation-content {
  width: 100%;
  height: 6.2rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  /* transition:all 0.5s ease; */
}
.confirmSignup .fillInformation-content .formBox {
  padding: 0.4rem 0.32rem 0 0.32rem;
}
.confirmSignup .fillInformation-content .fillTitle {
  font-size: 0.36rem;
  color: #333333;
  font-weight: 700;
}
.confirmSignup .fillInformation-content .iptBox {
  width: 100%;
  height: 0.68rem;
  padding-top: 0.22rem;
}
.confirmSignup .fillInformation-content .iptName {
  width: 1.2rem;
  font-size: 0.28rem;
  height: 0.46rem;
  line-height: 0.46rem;
  color: #222;
}
.confirmSignup .fillInformation-content .checkCode {
  border: 0.02rem solid #222222;
  color: #222222;
  width: 1.6rem;
  text-align: center;
  height: 0.46rem;
  line-height: 0.46rem;
  border-radius: 0.06rem;
}
.confirmSignup .fillInformation-content .checkCode-no {
  opacity: 0.5;
}
.confirmSignup .fillInformation-content input {
  border: none;
  width: 3.2rem;
  float: left;
  height: 0.46rem;
  line-height: 0.46rem;
  color: #333;
  font-size: 0.28rem;
  outline: none;
}
.confirmSignup .fillInformation-content .more {
  width: 0.2rem;
  margin-top: 0.1rem;
}
.confirmSignup .fillInformation-content .signBtn {
  width: 100%;
  height: 0.9rem;
  background-color: #222;
  color: #fff;
  line-height: 0.9rem;
  text-align: center;
  margin-top: 0.4rem;
  border-radius: 0.06rem;
}
.confirmSignup .fillInformation-content .signBtn-no {
  opacity: 0.5;
}
.confirmSignup .fillInformation .mask {
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.7;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
}
.confirmSignup .left {
  float: left;
}
.confirmSignup .right {
  float: right;
}
.confirmSignup .ml52 {
  margin-left: 0.52rem;
}
.confirmSignup .disc {
  width: 3%;
}
.confirmSignup .txt {
  width: 97%;
}
.confirmSignup .bottomBorder {
  border-bottom: 0.02rem solid #DDDDDD;
}
.confirmSignup .clear {
  clear: both;
}



