
.moreTeam-content {
  height: calc(100vh - 0.9rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
}
.moreTeam-content-list {
  width: calc(100% - 0.6rem);
  padding: 0.3rem 0;
  margin: 0 auto;
  height: 1rem;
  border-bottom: 1px solid #eee;
}
.moreTeam-content-list-img {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
  background-color: #eee;
  display: inline-block;
  margin-right: 0.1rem;
}
.moreTeam-content-list-img img {
  width: 1rem;
  height: 1rem;
  border-radius: 100%;
}
.moreTeam-content-list-info {
  height: 0.9rem;
  vertical-align: top;
  display: inline-block;
}
.moreTeam-content-list-info-table {
  height: 0.9rem;
  display: table;
}
.moreTeam-content-list-info-table-cell {
  display: table-cell;
  vertical-align: middle;
}
.moreTeam-content-list-info-table-cell-name {
  font-size: 0.3rem;
  color: #222;
  font-weight: bold;
}
.moreTeam-content-list-info-table-cell-num {
  font-size: 0.26rem;
  color: #999;
}
.moreTeam-content-list-btn {
  width: 1.2rem;
  height: 0.5rem;
  border-radius: 0.25rem;
  background-color: #EF4949;
  font-size: 0.28rem;
  text-align: center;
  line-height: 0.5rem;
  color: #fff;
  display: inline-block;
  vertical-align: top;
  margin-top: 0.25rem;
  float: right;
}
.moreTeam-btn {
  width: 100%;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  overflow: hidden;
  color: #fff;
  font-size: 0.3rem;
  background-color: #000;
  position: absolute;
  bottom: 0;
}



