
.myGift {
  height: 100vh;
  font-size: 0.28rem;
  position: relative;
}
.myGift-navigator {
  width: 100%;
  height: 0.92rem;
  overflow: hidden;
  white-space: nowrap;
  background-color: #fff;
  margin-bottom: 0.1rem;
  -webkit-box-shadow: 1px 1px 1px #eee;
          box-shadow: 1px 1px 1px #eee;
}
.myGift-navigator-lists {
  display: inline-block;
  width: 50%;
  height: 0.92rem;
  line-height: 0.92rem;
  text-align: center;
  position: relative;
  font-family: PingFang-SC-Medium;
}
.myGift-navigator-lists span {
  width: 0.52rem;
  height: 0.04rem;
  background-color: #222;
  position: absolute;
  bottom: 0;
  left: calc(50% - 0.26rem);
}
.myGift-content {
  width: 100%;
  height: calc(100vh - 1.02rem);
  overflow-y: auto;
  /* overflow-x: hidden; */
  -webkit-overflow-scrolling: touch;
}
.myGift-content-box {
  width: 100%;
  background-color: #fff;
}
.myGift-content-box-lists {
  width: calc(100% - 0.6rem);
  height: 1.8rem;
  padding: 0.32rem 0.3rem 0 0.3rem;
}
.myGift-content-box-lists-cont {
  width: 100%;
  height: 1.8rem;
  border-bottom: 0.02rem solid #DDDDDD;
}
.myGift-content-box-lists-cont-imgBox {
  float: left;
  width: 2.52rem;
  height: 1.52rem;
  overflow: hidden;
}
.myGift-content-box-lists-cont-imgBox img {
  width: 100%;
  border-radius: 0.06rem;
}
.myGift-content-box-lists-cont-contBox {
  float: left;
  width: 4.08rem;
  height: 1.52rem;
  position: relative;
  margin-left: 0.25rem;
}
.myGift-content-box-lists-cont-contBox-title {
  font-family: 'PingFang-SC-Medium';
  font-size: 0.3rem;
  color: #333;
}
.myGift-content-box-lists-cont-contBox-amount {
  position: absolute;
  bottom: 0;
  color: #666;
}
.myGift-content-box-lists-cont-contBox-amount span {
  float: left;
  margin-right: 0.35rem;
}



