
.offCourseDet {
  font-size: 0.28rem;
  position: relative;
  height: 100vh;
  /*.footer{
		margin-bottom: 1rem;
	}*/
}
.offCourseDet-no {
  opacity: 0;
}
.offCourseDet-content {
  width: 100%;
  height: calc(100vh - 0.98rem);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.offCourseDet-content-banner {
  width: 100%;
  height: 4.5rem;
  position: relative;
  -webkit-box-shadow: 1px 1px 1px #eee;
          box-shadow: 1px 1px 1px #eee;
}
.offCourseDet-content-banner-myshade {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  background-color: #000;
  opacity: 0.7;
  z-index: 8;
}
.offCourseDet-content-banner img {
  width: 100%;
  height: 4.5rem;
  display: block;
}
.offCourseDet-content-banner .countdownTitle {
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  left: 0;
  top: 1.74rem;
  color: #fff;
  font-size: 0.35rem;
  font-family: PingFang-SC-Regular;
  z-index: 10;
}
.offCourseDet-content-banner .countdownTime {
  display: block;
  position: absolute;
  width: 100%;
  text-align: center;
  top: 2.32rem;
  color: #FFF;
  font-size: 0.6rem;
  font-family: PingFang-SC-Medium;
  z-index: 10;
}
.offCourseDet-content-banner .countdownTime span {
  font-size: 0.3rem;
  margin-left: 0.1rem;
  margin-right: 0.1rem;
  font-family: PingFang-SC-Light;
}
.offCourseDet-content .infoContainer {
  width: calc(100% - 0.6rem);
  padding: 0 0.3rem;
  background-color: #fff;
  margin-bottom: 0.2rem;
}
.offCourseDet-content .infoContainer .offTitle {
  width: 100%;
  height: 1rem;
  color: #333333;
  font-size: 0.36rem;
  line-height: 1rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.offCourseDet-content .infoContainer .container {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
}
.offCourseDet-content .infoContainer .container .imgLeft {
  width: 0.33rem;
  margin: 0.25rem 0.2rem 0 0;
}
.offCourseDet-content .infoContainer .container .address_icon {
  width: 0.27rem;
}
.offCourseDet-content .infoContainer .container .my_imgs {
  margin: 0.3rem 0.2rem 0 0;
}
.offCourseDet-content .infoContainer .container .textCont {
  color: #666;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 90%;
}
.offCourseDet-content .infoContainer .container .imgRight {
  width: 0.35rem;
  margin-top: 0.25rem;
}
.offCourseDet .myLayer {
  position: relative;
  z-index: 998;
}
.offCourseDet .myLayer .shade {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.7;
  pointer-events: auto;
}
.offCourseDet .myLayer .cancelBox {
  display: table;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  pointer-events: none;
}
.offCourseDet .myLayer .cancelBox-mask {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}
.offCourseDet .myLayer .cancelBox-content {
  position: relative;
  display: inline-block;
  width: 4.22rem;
  height: 5.52rem;
  background-color: #fff;
  border-radius: 0.06rem;
  padding: 0 0.4rem;
  pointer-events: auto;
}
.offCourseDet .myLayer .cancelBox-content .close {
  width: 0.3rem;
  display: block;
  position: absolute;
  right: 0.1rem;
  top: 0.1rem;
  pointer-events: auto;
}
.offCourseDet .myLayer .cancelBox .cancelTitle {
  font-size: 0.36rem;
  color: #333;
  font-weight: 600;
  margin-top: 0.4rem;
  margin-bottom: 0.3rem;
}
.offCourseDet .myLayer .cancelBox p {
  text-align: left;
  font-size: 0.3rem;
}
.offCourseDet .myLayer .cancelBox .qrCode {
  width: 3.16rem;
  height: 3.16rem;
  margin: 0 auto;
}
.offCourseDet .myLayer .cancelBox .qrCode img {
  width: 100%;
  height: 100%;
}
.offCourseDet .myLayer .cancelBox .btn {
  width: 100%;
  margin-top: 0.4rem;
}
.offCourseDet-navigator {
  width: 100%;
  height: 0.9rem;
  background-color: #fff;
  overflow: hidden;
  white-space: nowrap;
  -webkit-box-shadow: 1px 1px 1px #eee;
          box-shadow: 1px 1px 1px #eee;
}
.offCourseDet-navigator-lists {
  display: inline-block;
  width: 50%;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  position: relative;
}
.offCourseDet-navigator-lists span {
  width: 0.52rem;
  height: 0.04rem;
  background-color: #222;
  position: absolute;
  bottom: 0;
  left: calc(50% - 0.26rem);
}
.offCourseDet-box {
  background-color: #fff;
}
.offCourseDet-box-cont {
  width: 100%;
}
.offCourseDet-box-cont img {
  width: 100%;
}
.offCourseDet .fillInformation {
  font-size: 0.28rem;
  position: relative;
}
.offCourseDet .fillInformation-content {
  width: 100%;
  height: 6.2rem;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 99;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.offCourseDet .fillInformation-content .formBox {
  padding: 0.4rem 0.32rem 0 0.32rem;
}
.offCourseDet .fillInformation-content .fillTitle {
  font-size: 0.36rem;
  color: #333333;
  font-weight: 700;
}
.offCourseDet .fillInformation-content .iptBox {
  width: 100%;
  height: 0.68rem;
  padding-top: 0.22rem;
}
.offCourseDet .fillInformation-content .iptName {
  width: 1.2rem;
  height: 0.46rem;
  line-height: 0.46rem;
  font-size: 0.28rem;
  color: #222;
}
.offCourseDet .fillInformation-content .checkCode {
  border: 0.02rem solid #222222;
  color: #222222;
  width: 1.6rem;
  text-align: center;
  height: 0.46rem;
  line-height: 0.46rem;
  border-radius: 0.06rem;
}
.offCourseDet .fillInformation-content .checkCode-no {
  opacity: 0.5;
}
.offCourseDet .fillInformation-content input {
  border: none;
  width: 3.2rem;
  float: left;
  height: 0.46rem;
  color: #333;
  font-size: 0.28rem;
  line-height: 0.46rem;
  outline: none;
}
.offCourseDet .fillInformation-content .more {
  width: 0.2rem;
  margin-top: 0.1rem;
}
.offCourseDet .fillInformation-content .signBtn {
  width: 100%;
  height: 0.9rem;
  background-color: #222;
  color: #fff;
  line-height: 0.9rem;
  text-align: center;
  margin-top: 0.4rem;
  border-radius: 0.06rem;
}
.offCourseDet .fillInformation-content .signBtn-no {
  opacity: 0.5;
}
.offCourseDet .fillInformation .mask {
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.7;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
}
.offCourseDet .footerNavigator {
  width: calc(100% - 0.52rem);
  height: 0.98rem;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #fff;
  padding-top: 0.14rem;
  padding-left: 0.52rem;
  font-size: 0.28rem;
  color: #fff;
  z-index: 7;
  border-top: 1px solid #eee;
}
.offCourseDet .footerNavigator .consultation img {
  width: 0.46rem;
}
.offCourseDet .footerNavigator .consultation span {
  display: block;
  font-size: 0.22rem;
  color: #333333;
}
.offCourseDet .footerNavigator .vipFree {
  font-size: 0.28rem;
  color: #fff;
  text-align: center;
  width: 2.6rem;
  height: 0.82rem;
  line-height: 0.82rem;
  background-color: #222;
  border-radius: 0.06rem;
  margin-left: 0.62rem;
}
.offCourseDet .footerNavigator .paid {
  font-size: 0.28rem;
  color: #fff;
  text-align: center;
  width: 2.6rem;
  height: 0.82rem;
  line-height: 0.82rem;
  border-radius: 0.06rem;
  background-color: #222;
  margin-left: 0.32rem;
}
.offCourseDet .footerNavigator .registered {
  font-size: 0.28rem;
  color: #fff;
  text-align: center;
  width: 5.7rem;
  height: 0.82rem;
  line-height: 0.82rem;
  border-radius: 0.06rem;
  background-color: #666;
  margin-left: 0.42rem;
}
.offCourseDet .footerNavigator .otherStatus {
  text-align: center;
  width: 6.43rem;
  height: 0.82rem;
  line-height: 0.82rem;
  border-radius: 0.06rem;
  background-color: #666;
}
.offCourseDet .left {
  float: left;
}
.offCourseDet .right {
  float: right;
}
.offCourseDet .ishide {
  display: none;
}
.offCourseDet .bottomBorder {
  border-bottom: 0.02rem solid #EEEEEE;
}



