
#app {
  /*font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;*/
  color: #333;
  height: 100vh;
  background: #f1f2f6;
}
#app .mint-navbar {
  top: 0;
  z-index: 2;
  width: 100%;
}
#app .detailsPage2 .mint-navbar {
  margin-top: 0.2rem;
}
#app .mint-tab-item-label {
  font-size: 0.22rem;
  position: relative;
  top: -0.02rem;
}
#app .detailsPage .mint-tab-item-label {
  font-size: 0.28rem;
  line-height: 0.88rem;
  /*color: #666;*/
}
#app .detailsPage2 .mint-tab-item-label {
  font-size: 0.28rem;
  line-height: 0.7rem;
}
#app .mint-tab-item-icon {
  width: 0.52rem;
}
#app .mint-tab-item-icon img {
  height: 0.48rem;
  width: auto;
  position: relative;
}
@media screen and (max-width: 375px) {
#app .mint-tab-item-icon img {
    top: 0.01rem;
}
#app .mint-tab-item-label {
    top: -0.05rem;
}
#app .mint-tab-item-icon {
    width: 0.52rem;
}
}
@media screen and (min-width: 414px) {
#app .detailsPage2 .mint-tab-item-label .line {
    position: relative;
    top: -0.1rem;
    border-radius: 0.02rem;
}
}
#app .mint-tabbar {
  background-color: #fff;
  border-top: 1px solid #eee;
  background-image: none;
}
#app .mint-tabbar > .mint-tab-item.is-selected {
  color: #222;
  background-color: #fff;
}
#app .mint-tabbar > .mint-tab-item.is-selected .mint-tab-item-label {
  color: #222;
}
#app .mint-tab-item {
  padding: 0.094rem 0 0.3rem;
}
#app .detailsPage2 .mint-tab-item {
  padding: 0.094rem 0;
}
#app .mint-swipe-indicator {
  border: 1px solid #fff;
  opacity: 1;
  width: 0.08rem;
  height: 0.08rem;
  background-color: transparent;
}
#app .mint-swipe-indicator.is-active {
  background-color: #fff;
}
#app .hotactivity .mint-swipe-indicator {
  border: 1px solid #666;
  opacity: 1;
  width: 0.08rem;
  height: 0.08rem;
  background-color: transparent;
}
#app .hotactivity .mint-swipe-indicator.is-active {
  background-color: #333;
}
.iconfont {
  font-size: 0.4rem;
}



