
.overdue {
  padding: 0 0.3rem;
  height: calc(100vh - 1.8rem);
  overflow-y: scroll;
}
.overdue-item {
  background: url('/static/img/home/coupon_bg2@2x.png');
  width: 100%;
  height: 1.76rem;
  background-size: 100% 100%;
  margin-top: 0.3rem;
  overflow: hidden;
}
.overdue-item-one {
  float: left;
  width: 31%;
}
.overdue-item-one p:nth-child(1) {
  color: #EF5D38;
  font-size: 0.38rem;
  text-align: center;
  margin-top: 0.5rem;
}
.overdue-item-one p:nth-child(2) {
  color: #666666;
  font-size: 0.24rem;
  text-align: center;
  margin-top: 0.05rem;
}
.overdue-item-two {
  float: left;
  width: 45%;
}
.overdue-item-two p:nth-child(1) {
  color: #222222;
  font-size: 0.3rem;
  margin-top: 0.36rem;
  text-align: left;
}
.overdue-item-two p:nth-child(2) {
  color: #666666;
  font-size: 0.24rem;
  text-align: left;
  margin: 0.05rem 0;
}
.overdue-item-two p:nth-child(3) {
  color: #666666;
  font-size: 0.24rem;
  text-align: left;
}
.overdue-item-three {
  float: left;
  width: 24%;
  height: 100%;
  position: relative;
}
.overdue-item-three img {
  width: 1.46rem;
  position: absolute;
  right: -0.2rem;
  top: 50%;
  margin-top: -0.73rem;
}



