
.notUser {
  padding: 0 0.3rem;
  height: calc(100vh - 1.8rem);
  overflow-y: scroll;
}
.notUser-item {
  background: url('/static/img/home/coupon_bg2@2x.png');
  width: 100%;
  height: 1.76rem;
  background-size: 100% 100%;
  margin-top: 0.3rem;
  overflow: hidden;
}
.notUser-item-one {
  float: left;
  width: 31%;
}
.notUser-item-one p:nth-child(1) {
  color: #EF5D38;
  font-size: 0.38rem;
  text-align: center;
  margin-top: 0.5rem;
}
.notUser-item-one p:nth-child(2) {
  color: #666666;
  font-size: 0.24rem;
  text-align: center;
  margin-top: 0.05rem;
}
.notUser-item-two {
  float: left;
  width: 45%;
}
.notUser-item-two p:nth-child(1) {
  color: #222222;
  font-size: 0.3rem;
  margin-top: 0.36rem;
  text-align: left;
}
.notUser-item-two p:nth-child(2) {
  color: #666666;
  font-size: 0.24rem;
  text-align: left;
  margin: 0.05rem 0;
}
.notUser-item-two p:nth-child(3) {
  color: #666666;
  font-size: 0.24rem;
  text-align: left;
}
.notUser-item-three {
  float: left;
  width: 24%;
  height: 100%;
  position: relative;
}
.notUser-item-three p {
  width: 1.1rem;
  height: 0.5rem;
  background-color: #EF5D38;
  border-radius: 0.26rem;
  text-align: center;
  color: white;
  font-size: 0.28rem;
  line-height: 0.5rem;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-top: -0.25rem;
  margin-left: -0.55rem;
}



