
#DetailsPage .goodsComponents{
	background: #f1f2f6;
}
.upDown{
	position: relative;
	top: -0.025rem;
}
.load{
	font-size: 0.221rem;
	text-align: center;
	color: #999;
}
.toubu{
	height: 1.5rem;
    position: fixed;
    width: 100%;
    z-index: 1;
}
#DetailsPage{
	width: 100%;
	background: #f1f2f6;
}
#DetailsPage .middle{
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 0.83rem;
	border: 1px solid #f1f2f6;
	background: #fff;
}
#DetailsPage .middle .searchInp{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	position: relative;
}
#DetailsPage .middle .searchInp input{
	border:0;
	background: #f1f2f6;
	width: 100%;
	padding: 0.1rem 0.7rem 0.1rem 0.7rem;
	font-size: 0.26rem;
	border-radius: 0.3rem;
	outline-style: none;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	position: absolute;
	left: 0;
	top: 0.15rem;
}
#DetailsPage .searchInp i{
	font-size: 0.35rem;
	position: absolute;
	color: #999;
	top:0.20rem;
	z-index: 1000;
}
#DetailsPage .icon-quxiaochacha{
	margin-top: 0.005rem;
}
#DetailsPage .icon-search{
	left: 0.1rem;
}
#DetailsPage .icon-quxiaochacha{
	right: 0.1rem;
}
#DetailsPage .icon-quxiaochacha:hover{
	color: #3385ff;
}
#DetailsPage .menu{
	font-size: 0.4rem;
	padding:0.16rem 0 0 0.2rem;
	color: #3385ff;
}
#DetailsPage .menu>i{
	font-size: 0.5rem;
	margin-right: 0.1rem;
}
#DetailsPage .middle>div{
	padding: 0.18rem 0.2rem 0 0;
	display: block;
}
#DetailsPage .search{
	font-size: 0.3rem;
	color: #3385ff;
	display: block;
	margin-left: 0.1rem;
}
#DetailsPage .theIndex{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-preferred-size: 100%;
	    flex-basis: 100%;
	height: 0.6rem;
}
#DetailsPage .theIndex>div{
	font-size: 0.26rem;
}
.thisBox{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	background: #fff;
	border-bottom: 1px solid #e1e2e6;
}
#DetailsPage .thisBox>div{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
}
#DetailsPage .every{
	text-align: center;
	color: #333;
	font-size: 0.26rem;
	padding-top: 0.1rem;
}
#DetailsPage .layout {
	padding: 0;
	text-align: center;
	/*flex-basis: 25%;*/
	color: #3385ff;
}
#DetailsPage .layout i{
	position: relative;
	top: 0.07rem;
	font-weight: bold;
}
#DetailsPage .group{
	font-size: 0.35rem;
}
#DetailsPage #mengceng{
	font-size: 0.3rem;
	background-color: rgba(0,0,0,0.6);
	display: none;
	position: fixed;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 10000;
	font-weight: bolder;
	text-align: center;
}
#DetailsPage #mengceng .navClassify{
	width: 40%;
	background-color: #fff;
	padding: 0rem 0 0.1rem;
	height: 100%;
	position: fixed;
	display: none;
	top: 0;
	left: -100%;
}
#DetailsPage .navClassify>div:nth-child(1){
	padding: 0.3rem 0 0.3rem 0.2rem;
	border-bottom:1px solid #e1e2e6;
	font-size: 0.27rem;
	font-weight: normal;
}
#DetailsPage .navClassify ul>li{
	padding: 0.3rem ;
}
#DetailsPage .navClassify ul>li:nth-child(1){
	padding: 0.3rem 0 0.3rem 0.2rem;
	border-bottom:0.01rem solid #ebebeb ;
}
#DetailsPage .classifyList>div{
	border-top: 0.01rem solid #ebebeb;
	margin-top:0.1rem;
}
#mengceng .navClassify li{
	position: relative;
	border-bottom: 1px solid #e1e2e6;
	font-size: 0.27rem;
	font-weight: normal;
}
#mengceng li>div{
	border-top:0.01rem solid #fff ;
}
#mengceng .navClassify li i{
	position: absolute;
	right: 0.1rem;
	top: 0.3rem;
	font-size: 0.4rem;
}
.goodsList{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 100%;
	margin: 0 auto;
	display: flex;
	text-align: left;
	min-height: calc(100vh - 3.2rem);
}
.eachGood{
	width: 50%;
	background-color: #fff;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border: 0.1rem solid #f1f2f6;
	/*padding: 0.1rem;*/
	border-collapse: collapse;
	position: relative;
	height: 4rem;
}
.goodsList_two{
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 100%;
	margin: 0 auto;
	text-align: left;
	min-height: calc(100vh - 3.2rem);
}
.eachGood_two{
	background-color:#fff;
	-ms-flex-preferred-size:100%;
	    flex-basis:100%;
	background-color:#fff;
	position: relative;
	height: 2.1rem;
	padding-right: 0.1rem;
	border-top: 1px solid #f1f2f6;
}
.eachGood_two:nth-child(1){
	margin-top: 0.1rem;
	border-bottom: 0;
}



