
.myEquipment1 {
  background-color: #fff;
  width: 100%;
  background-image: url('../../../static/img/home/my_equipmentbg.png');
  background-size: 100%;
  height: 100vh;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  color: #fff;
}
.myEquipment1-title {
  width: 4.5rem;
  height: 1.2rem;
  position: relative;
  text-align: center;
  font-size: 0.56rem;
  line-height: 1.2rem;
  margin: 0 auto;
  margin-top: 0.72rem;
  margin-bottom: 0.68rem;
}
.myEquipment1-title-img1 {
  height: 0.6rem;
  position: absolute;
  top: 0;
  left: 0;
}
.myEquipment1-title-img2 {
  height: 0.6rem;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  position: absolute;
  right: 0;
  bottom: 0;
}
.myEquipment1-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 0.34rem;
  color: #fff;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
}
.myEquipment1-body-line {
  width: 0.82rem;
  height: 0.02rem;
  background-color: #fff;
  margin-top: 0.18rem;
}
.myEquipment1-body img {
  height: 0.34rem;
  position: relative;
  top: -0.05rem;
}
.myEquipment1-body span {
  position: relative;
  top: -0.08rem;
}
.myEquipment1-bodylist {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  margin-top: 0.2rem;
}
.myEquipment1-bodylist-left {
  font-size: 0.3rem;
  width: 0.8rem;
  display: inline-block;
  vertical-align: top;
  line-height: 1.1rem;
  overflow: hidden;
}
.myEquipment1-bodylist-right {
  width: calc(100% - 0.8rem);
  display: inline-block;
  vertical-align: top;
  margin-left: -0.3rem;
}
.myEquipment1-bodylist-right-list,
.myEquipment1-bodylist-right-list1 {
  font-size: 0.28rem;
  border: 1px solid #999;
  display: inline-block;
  color: #fff;
  margin-bottom: 0.2rem;
}
.myEquipment1-bodylist-right-selected {
  background-color: #fff;
  color: #333;
}
.myEquipment1-bodylist-right-list {
  width: 1.8rem;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  overflow: hidden;
  margin-left: 0.18rem;
}
.myEquipment1-bodylist-right-list1 {
  padding: 0.34rem 0.15rem;
}
.myEquipment1-bodylist-right textarea {
  background-color: transparent;
  outline-style: none;
  border-radius: #999;
  width: calc(100% - 0.5rem);
  height: calc(2rem - 0.4rem);
  resize: none;
  padding: 0.2rem;
  caret-color: #fff;
  font-size: 0.3rem;
  color: #fff;
}
.myEquipment1-btn {
  font-size: 0.3rem;
  color: #222;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  text-align: center;
  line-height: 0.96rem;
  height: 0.96rem;
  border-radius: 0.06rem;
  margin-top: 0.4rem;
  margin-bottom: 0.5rem;
  cursor: pointer;
}



