
.giftBox {
  font-size: 0.24rem;
  color: #333;
  width: 100%;
  background-color: #fff;
  position: fixed;
  z-index: 2;
  bottom: 0rem;
  border-top-left-radius: 0.1rem;
  border-top-right-radius: 0.1rem;
  padding-bottom: 0.4rem;
}
.giftBox-title {
  font-size: 0.38rem;
  color: #333;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  padding: 0.4rem 0;
  border-bottom: 1px solid #eee;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-weight: bold;
}
.giftBox-num {
  font-size: 0.28rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.4rem 0.3rem;
  border-bottom: 1px solid #eee;
}
.giftBox-num-right {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
.giftBox-num-right-one,
.giftBox-num-right-three {
  width: 0.5rem;
  height: 0.5rem;
  border: 1px solid #CCCCCC;
  text-align: center;
  line-height: 0.5rem;
  color: #ccc;
  font-size: 0.36rem;
}
.giftBox-num-right-one {
  border-top-left-radius: 0.06rem;
  border-bottom-left-radius: 0.06rem;
}
.giftBox-num-right-three {
  border-top-right-radius: 0.06rem;
  border-bottom-right-radius: 0.06rem;
  color: #666;
}
.giftBox-num-right-two {
  width: 0.76rem;
  height: 0.5rem;
  border: 0;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  outline-style: none;
  -webkit-appearance: none;
  text-align: center;
  color: #333;
  border-radius: 0;
}
.giftBox-num-right-four {
  line-height: 0.5rem;
  margin-left: 0.2rem;
}
.giftBox-price {
  font-size: 0.28rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0.4rem 0.3rem;
}
.giftBox-price-right {
  font-weight: bold;
}
.giftBox-payBtn {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  height: 0.92rem;
  background-color: #222;
  border-radius: 1px;
  color: #fff;
  font-size: 0.3rem;
  text-align: center;
  line-height: 0.92rem;
  overflow: hidden;
  margin-top: 0.2rem;
  border-radius: 0.06rem;
}



