
.puzzleHome {
  width: 100%;
  height: 100%;
  position: fixed;
  background: url('/static/img/puzzle/ticket_bg.png');
  background-repeat: no-repeat;
  background-size: 100%;
}
.puzzleHome-content {
  height: calc(100vh - 0.92rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.puzzleHome-content-time {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 0.4rem;
}
.puzzleHome-content-time-item {
  height: 0.8rem;
  width: 1.12rem;
  background-image: url(/static/img/college/Countdown.png);
  background-size: 50%;
}
.puzzleHome-content-time-item-num {
  color: #fff;
  font-size: 0.7rem;
  margin-left: 0.1rem;
  letter-spacing: 0.16rem;
}
.puzzleHome-content-time-hao img {
  width: 0.07rem;
  margin: 0.25rem 0.1rem;
}
.puzzleHome .endTime {
  text-align: center;
  color: #2D3C50;
  font-size: 0.4rem;
  font-weight: 900;
  padding-top: 2.6rem;
}
.puzzleHome .card {
  width: 2.82rem;
  height: 0.72rem;
  background: #EF4949;
  border-radius: 0.36rem;
  color: white;
  font-size: 0.28rem;
  line-height: 0.72rem;
  text-align: center;
  margin: 0.5rem auto 0;
  -webkit-box-shadow: 0px 1px 2px #EF4949;
          box-shadow: 0px 1px 2px #EF4949;
}
.puzzleHome .explain {
  background: url("/static/img/college/buypage/Inviting_camera.png");
  width: 6.18rem;
  height: 4.48rem;
  background-size: 100% 100%;
  margin: 0.6rem auto 0.5rem;
}
.puzzleHome .explain div {
  text-align: center;
  font-size: 0.32rem;
  color: #000000;
  padding-top: 0.26rem;
  padding-right: 0.2rem;
}
.puzzleHome .explain ul {
  padding-left: 0.92rem;
  padding-right: 0.42rem;
  /*padding-top: 0.2rem;*/
}
.puzzleHome .explain ul li {
  font-size: 0.26rem;
  color: #333333;
  list-style: disc;
  line-height: 0.38rem;
  margin-top: 0.2rem;
}
.puzzleHome .collegeFooter {
  width: 100%;
  height: 0.92rem;
  position: fixed;
  bottom: 0rem;
}
.puzzleHome .collegeFooter .left {
  width: 55%;
  height: 100%;
  background-color: white;
  float: left;
  text-align: center;
  color: #333333;
  font-size: 0.26rem;
  line-height: 0.92rem;
}
.puzzleHome .collegeFooter .right {
  width: 45%;
  height: 100%;
  float: right;
  background-color: #EF4949;
  text-align: center;
  color: white;
  font-size: 0.26rem;
  line-height: 0.92rem;
}
.puzzleHome .puzzleCode .codeBg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  position: fixed;
  top: 0;
  left: 0;
}
.puzzleHome .puzzleCode .codeContent {
  width: 5.02rem;
  height: 5.52rem;
  background-color: white;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -2.76rem;
  margin-left: -2.51rem;
}
.puzzleHome .puzzleCode .codeContent .cancel {
  padding-top: 0.1rem;
  padding-right: 0.32rem;
  font-size: 0.36rem;
  color: #cccccc;
  text-align: right;
  position: relative;
  top: 0.1rem;
}
.puzzleHome .puzzleCode .codeContent .title {
  font-size: 0.38rem;
  color: #333333;
  text-align: center;
  font-weight: 900;
}
.puzzleHome .puzzleCode .codeContent .code {
  width: 3.16rem;
  height: 3.16rem;
  margin: 0.26rem auto;
}
.puzzleHome .puzzleCode .codeContent .code img {
  width: 100%;
  height: 100%;
}
.puzzleHome .puzzleCode .codeContent .prompt {
  font-size: 0.3rem;
  color: #333333;
  text-align: center;
}



