
.exchange-bg {
  width: 100vh;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 200;
}
.exchange-comtent {
  width: 6rem;
  height: 3.8rem;
  background-color: #F5F5F5;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -1.9rem;
  margin-left: -3rem;
  border-radius: 0.06rem;
  z-index: 201;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.exchange-comtent-title {
  position: relative;
  padding: 0.4rem 0;
}
.exchange-comtent-title div:nth-child(1) {
  font-size: 0.38rem;
  color: #222222;
  width: 100%;
  text-align: center;
}
.exchange-comtent-title div:nth-child(2) {
  position: absolute;
  right: 0.3rem;
  font-size: 0.38rem;
  color: #CCCCCC;
  top: 0.42rem;
}
.exchange-comtent-input input {
  border: none;
  width: 5.4rem;
  height: 0.9rem;
  margin-left: 0.3rem;
  color: #CCCCCC;
  padding: 0 0.2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.exchange-comtent-input ::-webkit-input-placeholder {
  color: #CCCCCC;
}
.exchange-comtent-btn {
  width: 5.4rem;
  height: 0.9rem;
  margin-left: 0.3rem;
  background-color: #222222;
  border-radius: 0.06rem;
  font-size: 0.3rem;
  line-height: 0.9rem;
  text-align: center;
  color: white;
  margin-top: 0.35rem;
}



