
.oncourseDet {
  height: 100vh;
  font-size: 0.28rem;
  position: relative;
}
.oncourseDet-no {
  opacity: 0;
}
.oncourseDet-content {
  width: 100%;
  height: calc(100vh - 0.9rem);
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}
.oncourseDet-content-cover {
  width: 100%;
  height: 4.5rem;
  position: relative;
  background-color: #000;
  -webkit-box-shadow: 1px 1px 1px #eee;
          box-shadow: 1px 1px 1px #eee;
}
.oncourseDet-content-cover-fen {
  width: 100%;
  height: 100%;
}
.oncourseDet-content-cover-play {
  height: 1rem;
  width: 1rem;
  left: calc(50% - 0.5rem);
  top: calc(50% - 0.5rem);
  position: absolute;
}
.oncourseDet-content-cover .courseTitle {
  font-size: 0.36rem;
  color: #fff;
  position: absolute;
  left: 0.3rem;
  bottom: 0.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 80%;
}
.oncourseDet-navigator {
  width: 100%;
  height: 0.9rem;
  background-color: #fff;
  overflow: hidden;
  white-space: nowrap;
}
.oncourseDet-navigator-lists {
  display: inline-block;
  width: 50%;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  position: relative;
}
.oncourseDet-navigator-lists span {
  width: 0.52rem;
  height: 0.04rem;
  background-color: #222;
  position: absolute;
  bottom: 0;
  left: calc(50% - 0.26rem);
}
.oncourseDet-box {
  overflow: auto;
  /*
		height:calc(~"100% - 5.5rem");*/
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.oncourseDet-box .intro {
  background-color: #fff;
  width: 100%;
}
.oncourseDet-box .intro img {
  width: 100%;
}
.oncourseDet-box .directory {
  background-color: #fff;
  padding: 0 0.3rem 0 0.28rem;
}
.oncourseDet-box .directory .directoryLists {
  position: relative;
  width: 100%;
  height: 0.64rem;
  padding: 0.38rem 0 0 0;
}
.oncourseDet-box .directory .directoryLists .noticeImg,
.oncourseDet-box .directory .directoryLists .listNum {
  width: 0.5rem;
  text-align: center;
}
.oncourseDet-box .directory .directoryLists .directoryTitle {
  font-size: 0.26rem;
  margin-left: 0.2rem;
  width: 5.8rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.oncourseDet-box .directory .directoryLists .playImg {
  width: 0.32rem;
}
.oncourseDet .shade {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.7;
  z-index: 8;
}
.oncourseDet .cancelBox {
  display: table;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9;
}
.oncourseDet .cancelBox-mask {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  z-index: 9;
}
.oncourseDet .cancelBox-content {
  position: relative;
  display: inline-block;
  width: 4.22rem;
  height: 5.52rem;
  background-color: #fff;
  z-index: 999;
  border-radius: 0.06rem;
  padding: 0 0.4rem;
}
.oncourseDet .cancelBox-content .close {
  width: 0.3rem;
  display: block;
  position: absolute;
  right: 0.1rem;
  top: 0.1rem;
}
.oncourseDet .cancelBox .cancelTitle {
  font-size: 0.36rem;
  color: #333;
  font-weight: 600;
  margin-top: 0.4rem;
  margin-bottom: 0.3rem;
}
.oncourseDet .cancelBox p {
  text-align: left;
  font-size: 0.3rem;
}
.oncourseDet .cancelBox .qrCode {
  width: 3.16rem;
  height: 3.16rem;
  margin: 0 auto;
}
.oncourseDet .cancelBox .qrCode img {
  width: 100%;
  height: 100%;
}
.oncourseDet .cancelBox .btn {
  width: 100%;
  margin-top: 0.4rem;
}
.oncourseDet .signupOnSite {
  width: 100%;
  height: 0.9rem;
  line-height: 0.9rem;
  position: absolute;
  left: 0;
  bottom: 0;
  text-align: center;
  color: #fff;
  background-color: #222;
  font-size: 0.28rem;
}
.oncourseDet .left {
  float: left;
}
.oncourseDet .right {
  float: right;
}
.oncourseDet .roundButton {
  border-radius: 0.34rem;
  border: 0.02rem solid #fff;
  position: absolute;
  color: #fff;
  font-size: 0.28rem;
  text-align: center;
}
.oncourseDet .borderTop {
  border-top: 0.02rem solid #EEEEEE;
}



