
.recordList-list {
  height: 1.76rem;
  width: 100%;
  background-color: #fff;
}
.recordList-list-box {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  height: calc(1.76rem - 1px);
  border-bottom: 1px solid #eee;
  overflow: hidden;
}
.recordList-list-box-img {
  width: 1.12rem;
  height: 1.12rem;
  border-radius: 50%;
  background-color: #eee;
  display: inline-block;
  margin-top: 0.27rem;
}
.recordList-list-box-img img {
  width: 1.12rem;
  height: 1.12rem;
  border-radius: 50%;
}
.recordList-list-box-name {
  display: inline-block;
  font-size: 0.26rem;
  vertical-align: top;
  line-height: 1.76rem;
  margin: 0 0.1rem;
  width: 0.8rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.recordList-list-box-info {
  width: 4.3rem;
  display: inline-block;
  vertical-align: top;
  font-size: 0.26rem;
}
.recordList-list-box-info-table {
  display: table;
  height: 1.76rem;
}
.recordList-list-box-info-table-cell {
  display: table-cell;
  vertical-align: middle;
}
.recordList-list-box-info-table-cell-title {
  margin-bottom: 0.08rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  width: 4.5rem;
}
.recordList-list-box-info-table-cell-time {
  color: #666;
}
.recordList-list-box-info-table-cell-time span:nth-child(2) {
  float: right;
  position: relative;
  top: 0.03rem;
}



