
.myEquipment {
  background-color: #fff;
  width: 100%;
  height: 100vh;
}
.myEquipment-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 0.34rem;
  color: #222;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
}
.myEquipment-body-line {
  width: 0.82rem;
  height: 0.02rem;
  background-color: #222;
  margin-top: 0.18rem;
}
.myEquipment-body img {
  height: 0.34rem;
  position: relative;
  top: -0.05rem;
}
.myEquipment-body span {
  position: relative;
  top: -0.08rem;
}
.myEquipment-bodylist {
  width: calc(100% - 0.6rem);
  margin: 0 auto;
}
.myEquipment-bodylist-left {
  font-size: 0.3rem;
  width: 0.8rem;
  display: inline-block;
  vertical-align: top;
  line-height: 1.1rem;
  overflow: hidden;
}
.myEquipment-bodylist-right {
  width: calc(100% - 0.8rem);
  display: inline-block;
  vertical-align: top;
  margin-left: -0.3rem;
}
.myEquipment-bodylist-right-list,
.myEquipment-bodylist-right-list1 {
  font-size: 0.28rem;
  border: 1px solid #999;
  display: inline-block;
  color: #666;
  margin-bottom: 0.2rem;
}
.myEquipment-bodylist-right-list {
  width: 1.8rem;
  height: 0.9rem;
  text-align: center;
  line-height: 0.9rem;
  overflow: hidden;
  margin-left: 0.18rem;
}
.myEquipment-bodylist-right-list1 {
  padding: 0.34rem 0.15rem;
}
.myEquipment-top {
  height: calc(100vh - 1.76rem);
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
}
.myEquipment-bottom {
  height: 1.76rem;
  width: 100%;
  position: fixed;
  bottom: 0;
}
.myEquipment-btn {
  font-size: 0.3rem;
  color: #fff;
  background-color: #222;
  width: calc(100% - 0.6rem);
  margin: 0 auto;
  text-align: center;
  line-height: 0.96rem;
  height: 0.96rem;
  border-radius: 0.06rem;
  margin-top: 0.4rem;
}



